/* -------------------- Google Font -------------------- */
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400,700&display=swap');

/* -------------------- CSS Reset -------------------- */
@import-normalize;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}

button{
  cursor: pointer;
  background: transparent;
  border: none;
}
  
button:hover, button:focus{
  outline: none;
}

html{
  font-size: 16px;
  font-family: 'Roboto Mono', monospace;
}

body{
  max-width: 100%;
  font-family: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

#root{
  width: 100%;
  height: 100vh;
}

@media only screen and (min-width: 426px) {
  body {
    width: 100%;
    height: 100vh;
  }
  
  #root{
    width: 375px;
    height: 667px;
    box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
  }
}

@media only screen and (min-width: 426px) and (min-height: 812px) {
  #root{
    height: 812px;
  }
}