@import '../../Default.scss';

.Keypad{
    padding: $small $padding;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background: $white;

    flex: 1 0 auto;

    transition: $transition;
}

.Row{
    height: 100%;
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
}

.DarkMode{
    background: $black;
}