@import '../../Default.scss';

.History{
    height: auto;
    overflow-y: auto;
    @extend %flex-center-spaceBetween;
    flex-direction: column;

    padding: 0 $padding;
}

.Button{
    width: 100%;
    
    border-radius: $borderRadius;
    background: $red;
    color: $white;
    font-size: $extraSmall;
    font-weight: 600;

    padding: 0.75rem;
    margin-bottom: 0.75rem;

    transition: $transition;
}

.Hidden{
    visibility: hidden;
}

.List{
    width: 100%;
    list-style-type: none;

    @extend %flex-center-spaceBetween;
    flex-direction: column;
}

.ListItem{
    width: 100%;
    @extend %flex-center-spaceBetween;
    justify-content: center;
    margin: 10px;
    text-align: center;
    background: $grey;
    border-radius: $borderRadius;
    padding: 10px;
}

.Total{
    color: $blue;
    font-weight: bold;
    margin-left: 15px;
}

.Calculation{
    color: $lightGrey;
    font-weight: normal;
}

.TextSmallest{
    font-size: 0.75rem;
}

.TextSmaller{
    font-size: 1rem;
}

.TextSmall{
    font-size: $extraSmall;
}

.TextNormal{
    font-size: $small;
}